@import "../../styles/base";

.modal {
	height: 100vh;
	width: 100%;
	background-color: rgba(0, 0, 0, 0.4);
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	opacity: 0;
	pointer-events: none;
	transition: 0.5s;
}

.modal.active {
	opacity: 1;
	pointer-events: all;
}

.modal__content {
	padding: 20px;
	border-radius: 12px;
	background-color: $color-page-background;
	transform: scale(0.5);
	transition: 0.4 all;
}

.modal__content.active {
	transform: scale(1);
}

.modal-links {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 30px;

	& a {
		font-size: 35px;
		padding: 20px;

		& svg:hover {
			opacity: 0.5;
		}
	}
}