@import "../../styles/base";

.header {
	padding: 40px 0;
	min-height: 695px;

	display: flex;
	justify-content: center;
	align-items: center;

	background-image: linear-gradient(rgba(18, 73, 199, 0.5), rgba(255, 255, 190, 0.3));
		// url();
                  
	background-position: center center;
	background-repeat: no-repeat;
	background-color: transparent;
	color: $color-white;
	text-align: center;
}

.header__wrapper {
	padding: 0 15px;
	max-width: 660px;
}

.header__title {
	margin-bottom: 20px;
	font-size: 40px;
	font-weight: 700;
	line-height: 1.4;
}

.header__title strong {
	font-size: 60px;
	font-weight: 700;
}

.header__title em {
	font-style: normal;
	color: $color-main;
}

.header__text {
	margin-bottom: 40px;
	font-size: 25px;
	line-height: 1.333;
}

.header__text p + p {
	margin-top: 0.5em;
}

